.postBtn {
  text-decoration: none;
  font-weight: bolder;
  color: rgb(255, 255, 255);
  padding: 10px;
  background-color: rgb(124, 119, 118);
  border-radius: 10px;
  margin: 10px;
}

.postBtn:hover {
  cursor: pointer;
  padding: 10px;
  background-color: rgb(51, 36, 36);
  color: rgb(209, 243, 165);
  border-radius: 10px;
}

.form-row {
  display: block;
}

.inputPurchasePrice {
  width: 35px;
  margin: 0px;
}

.form-column {
  text-align: center;
  margin: 10px;
}

.commentSection {
  display: flex;
  justify-content: center;
}

textarea.textbox {
  width: 800px;
  height: 100px;
  margin-top: 1rem;
  outline: none;
  resize: none;
}

.postButton,
.getUpdatedMarketPrices {
  display: flex;
  margin: 2rem;
  justify-content: center;
}

.inputTickerSymbol {
  width: 90px;
  margin: 0px;
}

@media screen and (min-width: 450px) {
  textarea.textbox {
    width: 800px;
    height: 150px;
    margin-top: 2rem;
  }
}

@media screen and (min-width: 620px) {
  textarea.textbox {
    width: 800px;
    height: 200px;
    margin-top: 3rem;
  }
  .form-row {
    display: flex;
    justify-content: space-evenly;
  }
}

.deleteBtn {
  text-decoration: none;
  font-weight: bolder;
  color: rgb(255, 255, 255);
  padding: 10px;
  background-color: rgb(124, 119, 118);
  border-radius: 10px;
  margin: 10px;
}

.deleteBtn:hover {
  cursor: pointer;
  padding: 10px;
  background-color: rgb(51, 36, 36);
  color: rgb(243, 133, 106);
  border-radius: 10px;
}

.holdingsTable {
  border: solid 1px rgb(175, 166, 166);
  border-collapse: collapse;
  border-spacing: 0;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
  font-size: 15px;
}

.holdingsTable thead th {
  background-color: rgb(110, 100, 100);
  border: solid 1px rgb(255, 255, 255);
  color: rgb(255, 255, 255);
  padding: 5px;
  text-align: left;
}

.holdingsTable tbody td {
  color: #333;
  padding: 10px;
}

.trow:hover,
.deleteBtn {
  cursor: pointer;
}

.trow:hover {
  background-color: rgb(226, 226, 226);
}

.price,
.roi {
  text-align: center;
}

@media screen and (min-width: 450px) {
  .holdingsTable {
    margin-top: 2rem;
    font-size: 18px;
  }
  .holdingsTable thead th {
    padding: 10px;
  }
  .links {
    font-size: 18px;
    margin-left: 18px;
  }
}

